

.wrap-top{
  display:flex;
  height: 200px;
  margin:auto;
  margin-top:75px;
  flex-flow: row nowrap;
  width:90%;
}

.foto-port {
  flex-basis: 10%;
  height: 160px;
  width: 145px;
  border:none;
  float:right;
  margin-left:12%;
}

.h1-div-home h1{
  flex-basis: 85%;
  padding: 50px;
  /*color:rgb(231, 157, 18);*/
  color: black;
  font-size: 20px;
  font-weight: normal;
}

.center-wrap {
  height: 350px;
  margin-top: 35px;
}

.microodles {
  display:block;
  border:none;
  margin:35px auto;
  padding: 35px;
  height: 286px;
  width:308px;
}

.btn-div {
  text-align:center;
  }

.btn-home{
  border-radius: 5px;
  border: 1px solid #ca8e03;
  padding: 0.7em 1.5em;
  font-size: 1em;
  font-style: italic;
  font-weight: bold;
  margin: 0 1em;
  margin-bottom: 1em;
  min-width: 10em;
  color:black;
}

.btn-home:hover {
  background-color:rgb(214, 199, 199);
  color:white;
}

