.App {
  text-align: center;
  font-family: 'PT Serif', serif;
}

.App-header {
  background: rgb(245,242,242);
  background: linear-gradient(45deg, rgba(245,242,242,1) 35%, rgba(242,224,235,1) 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.bar-div{
  padding-right: 10%;
  font-family: 'PT Serif', serif;
  display: flex;
  flex-flow: row nowrap;
}

.logo-div{
  flex-basis: 60%;
  margin-left: 100px;
  margin-top: 10px;  
  text-align: left;
}

.leaf{

width: 82px;
  height: 35px;
  -moz-border-radius: 0 62% / 0 88%;
  -webkit-border-radius: 0 50% / 0 100%;
  border-radius: 0 50% / 0 100%;
  border: 2.5px solid #555;
  

  margin-top:20px;
  display: flex;
  justify-content:center;
  overflow: visible;
  position: relative;
  transform: rotate(-15deg);
  transition: transform .4s ease-in-out;
  flex-shrink: 0;

  box-shadow: 2px 2px 2px 0px rgb(218, 213, 213);
  font-weight: bold;
}

.leaf:hover{
  transform: rotate(-195deg);
}

.logo-text{
  display: flex;
  align-items: center;
  justify-content: center;
  transform: rotate(15deg);
}

.leaf:hover .logo-text{
  transform: rotate(195deg);
}

.leaf .logo-text{
  transition: transform .4s ease-in-out; 
}

nav{
  flex-basis: 40%;
  text-align: right;
}

nav ul{
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: right;
  font-size:1.05em;
  font-weight: bold;
}

nav li {
  display: inline-block;
}

nav a {
  text-decoration:none;
  color: rgb(44, 40, 40);
  display: block;
  padding:20px 0 0;
  margin: 0 15px;
  line-height:18px;
}

nav a:hover {
  border-bottom: 2px solid orange;
}

.active {
  font-size:1.1em;
  border-bottom: 2px solid orange;
}

footer{
  height: 125px;
  width: 85%;
  margin: auto;
  text-align: center;
  border-top: 1px black dotted;
  margin-top: 150px;
  margin-bottom: 50px;
  padding-top:20px;
}
